a {
  color: var(--primary-color);
  text-decoration: none;
}

.InlineLink {
  font-weight: 500;
}

.InlineLink:hover {
  text-decoration: underline;
}

.IconLink {
  grid-column: 1 / -1;
  justify-self: start;
  display: flex;
}

main {
  padding: 3rem 2rem;
  min-height: calc(100vh - 4rem  - 1px);
  box-sizing: border-box;
}

h1 {
  font-size: 2rem;
  margin-top: 0;
}

h3 {
  margin: 0;
}

form {
  display: grid;
  gap: 1rem;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

label {
  font-size: 1.2rem;
  font-weight: 500;
  position: relative;
}

input, textarea, select {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  background: var(--border-color);
  border: none;
  border-radius: 0.4rem;
  padding: 0.4rem 0.6rem;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

input:focus, textarea:focus, select:focus {
  box-shadow: 0 0 0.14rem var(--primary-color);
  outline: none;
}

select {
  font-size: 1rem;
}

.RequiredField:after {
  content:"*";
  color: var(--primary-color);
  font-size: 0.9rem;
  position: absolute;
}

.DynamicInput {
  width: max(50%, 40rem);
}

.InvalidInput {
  background: rgba(250, 0, 63, 0.1);
}

.InvalidInput:focus {
  box-shadow: 0 0 0.14rem var(--danger);
}

.Error {
  color: var(--danger);
  font-size: 0.85rem;
  margin: 0;
  display: block;
}

.Error.Hidden {
  display: none;
}

button {
  font-family: 'Inter', sans-serif;
  background: transparent;
  border: none;
  cursor: pointer;
}

.Btn {
  font-size: 1.1rem;
  border-radius: 0.3rem;
  padding: 0.4rem 0.7rem;
  transition: all 0.3s ease;
}

.PrimaryBtn {
  background: var(--primary-color);
  color: white;
  font-weight: 500;
}

.PrimaryBtn:hover {
  background: var(--secondary-color);
  color: var(--primary-color);
  box-shadow: -3px -3px 0 var(--primary-color);
}

.SecondaryBtn {
  background: var(--border-color);
  color: black;
}

.SecondaryBtn:hover {
  background: #c2c2c2;
  color: white;
  box-shadow: -4px -4px 0 var(--border-color);
}

.ButtonGroup {
  display: flex;
  gap: 0.75rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.PopupMsg {
  background: var(--secondary-color);
  padding: 0.5rem 1rem;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  text-align: center;
}

.PopupMsg > button {
  height: 1rem;
  color: black;
  position: absolute;
  top: 0;
  right: 1rem;
}

@media screen and (max-width: 1024px) {
  .DynamicInput {
    width: auto;
  }
}

@media screen and (max-width: 480px) {
  main {
    padding: 3rem 1rem;
  }
}
